<template>
  <div id="message" style="margin-top:60px;">
    <my-header :child="type"></my-header>
     <section class="main">
      <div class="des-top"></div>
       <div class="des-content">
        <div class="content" style="margin-top: -16px;">
          <div class="message-content">
            <div class="dis">
                <div class="liuyan">
                  <div v-for="(p,index) in 10" :key="index">
                    <ul style="float:left;">
                      <li class="name">刘晓冬</li>
                      <li>来啊一起玩啊</li>
                    </ul>
                    <ul style="float:right;text-align:right;">
                      <li class="name">我</li>
                      <li>滚，自己玩去</li>
                    </ul>
                  </div>
                </div>
            </div>
            <el-button type="primary" @click="isShow = true" style="margin: 9px 0 0 44px;">我要留言吐槽</el-button>
            <el-dialog
              title="提示"
              :visible.sync="isShow"
              width="30%"
              center>
              <el-form>
                <el-form-item>
                  <el-input v-model="form.name" placeholder="请留下大名"></el-input>
                </el-form-item>

                <el-form-itme>
                  <el-input
                    type="textarea"
                    :autosize="{ minRows: 2, maxRows: 4}"
                    placeholder="请输入内容"
                    v-model="form.content">
                  </el-input>

                </el-form-itme>
              </el-form>

              <span slot="footer" class="dialog-footer">
                <el-button @click="isShow = false">取 消</el-button>
                <el-button type="primary" @click="isShow = false">确 定</el-button>
              </span>
            </el-dialog>



          </div>
        </div>
       </div>
      <div class="des-bottom"></div>
    </section>
  </div>
</template>

<script>
import Header from "../header";

export default {
  name: "app",
  components: {
    myHeader: Header
  },
  data() {
    return {
      isShow:false,
      msg: "我的博客",
      type: "message",
      form: {
        name: "",
        content: ""
      }
    };
  }
};
</script>

<style scoped>
.message-content {
  width: 100%;
  height: 650px;
  border-radius: 10px;
  background-color: #184b67;
  padding: 10px;
}
.liuyan {
  width: 917px;
  margin: 0 auto;
  color: white;
  height: 570px;
  overflow: auto;
}
.liuyan > div {
  width: 96%;
  background-color: aliceblue;
  color: black;
  border-radius: 5px;
  margin: 10px auto;
  padding: 10px;
  box-sizing: border-box;
}
.liuyan > div::after {
  display: block;
  content: "";
  clear: both;
}
/* .liuyan>div:hover{
  transform: scale(1.2);
} */
.liuyan > div ul {
  width: 100%;
}
.dis {
  width: 900px;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 5px;
  background: #7e7e7ab0;
}
</style>






